import React,{Component} from 'react';
import { Flex, Modal, Toast } from 'antd-mobile';
import Header from '../../components/header';
import BtnBottom from '../../components/btnbotm';

const prompt = Modal.prompt;

class MinePrice extends Component{
    state = {
        lists:[{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        },{
            time: new Date().toLocaleString(),
            desc: "视频打赏",
            coin: "5"
        }]
    }
    getList = () =>{
        return(
            this.state.lists.map((item,index) => 
            <Flex className="text-center table-item" key={index}>
                <Flex.Item>
                    <small className="lineH-low">{item.time}</small>
                </Flex.Item>
                <Flex.Item>
                    <small className="lineH-low">{item.desc}</small>
                </Flex.Item>
                <Flex.Item>
                    <small className="lineH-low">{item.coin}元</small>
                </Flex.Item>
            </Flex>
            )
        )
    } 
    btnCLick = () =>{
       prompt(
            '余额提现',
            '您当前账户余额为￥500.00,最多可提现500元',
            [
                { text: '取消' },
                { text: '提交', 
                    onPress: price =>  {
                        Toast.loading('提现中...',1)
                        setTimeout(() => {
                            Toast.success('提现成功',.5)
                        },1000)
                    }
                }],
                'default',
            )
    }
    render(){
        return(
            <div className="MinePrice">
                <Header back title="我的收支" />
                <div className="paddTB paddLR white mine-price-box" style={{background:`url(${require('../../assets/img/record-banner.jpg')})  no-repeat`,backgroundSize:"cover"}}>
                    <img src={require('../../assets/img/coin.png')} alt="coin" width="15%" />
                    <big>账上余额 ￥500.00 元</big>
                </div>
                <div className="price-table">
                    <Flex className="text-center table-item">
                        <Flex.Item>时间日期</Flex.Item>
                        <Flex.Item>收支说明</Flex.Item>
                        <Flex.Item>金额</Flex.Item>
                    </Flex>
                    {this.getList()}
                </div>
                <BtnBottom click={()=>this.btnCLick} title="余额提现" />
            </div>   
        )
    }
} 
export default MinePrice;


