import React, { Component } from 'react';
import ReactSwipe from 'react-swipe';
import banner1 from '../assets/img/banner1.jpg';
import banner2 from '../assets/img/banner2.jpg';
import banner3 from '../assets/img/banner3.jpg';

import Header from '../components/header';
import Tabbar from '../components/tabbar';

class Home extends Component{
    state = {
        images: [banner1, banner2, banner3],
        active: 0,
        courseHalf: [{
            name: '视频云建设',
            desc: '提升资源使用效率,为数据的融通提供可能',
            price: '￥10.00',
            num: '100',
            color: '#0070bb'
        },{
            name: '安防生态系统',
            desc: '解决海量视频图像信息大数据和AI处理的算力问题',
            price: '￥50.00',
            num: '220',
            color:'#ff4545'
        }],
        courses:[{
            name: '人工智能',
            desc: '开放的云模式构建繁荣的生态,云端智能化比前端智能化更强大、更丰富',
            price: '￥20.00',
            num: '1000',
            color: '#0070bb',
            src: banner2
        },{
            name: '物联网',
            desc: '进入云时代，安防行业引入IT领域的云计算、大数据、物联网等技术为己所用',
            price: '￥32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '大数据',
            desc: 'IT行业的开放创新、合作共赢的精神也在潜移默化的影响安防行业秩序',
            price: '￥23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '云计算',
            desc: '在通信和互联网圈的生态系统建设，也成为时下安防行业发展的一个热门课题',
            price: '￥40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        }]
    }
    showImage = () => {
        return (
            this.state.images.map((item,index) => 
                <img src={item} key={index} alt="banner"/>
            )
        )
    }
    showNum = () => {
        return (
            this.state.images.map((item,index) => 
               (index === this.state.active) ?  <div className="active" key={index} /> : <div key={index} />
            )
        )
    }
    changeActive = (index) => {
        this.setState({
            active: index
        })
    }
    getCourseHalf = () => {
        return(
            this.state.courseHalf.map((item,index) => 
            <div key={index} onClick={()=>this.goDetail(item)} className="text-l" style={{background:item.color,boxShadow:"0 4px 16px 0 "+item.color}} >
                <div>{item.name}</div>
                <small className="lineH-low white">{item.desc}</small>
                <div>
                    <small>{item.price} <span className="fr">{item.num}人</span></small>
                </div>
            </div>
            )
        )
    }
    getCourse = () => {
        return(
            this.state.courses.map((item,index) => 
             <div key={index} onClick={()=>this.goDetail(item)} className="paddLR">
                <div className="fl" style={{width:"38%",marginRight:"2%"}}>
                    <img src={item.src} alt="peitu" width="100%" style={{borderRadius:"10px",margin:"5% 0 0" }}/>
                </div>
                <div key={index}  className="fl text-l" style={{color:"#000",width:"60%"}} >
                    <div>{item.name}</div>
                    <small className="lineH-low">{item.desc}</small>
                    <div>
                        <small>{item.price} <span className="fr">{item.num}人在学</span></small>
                    </div>
                </div>
                <div className="clear" />
             </div>
            )
        )
    }
    goDetail = (item) => {
        let hrefStr = [];
        for(let i in item){
          hrefStr.push(`${i}=${item[i]}`) 
        }
        window.location=`#/detail?${encodeURI(hrefStr.join('&'))}`;
      }
      goCourse = () => {
        window.location = '#/course';
       
      }
    render(){
        return (
            <div className="Home">
                {/* 头部Header */}
                <Header />
                {/* swipe图片 */}
                <ReactSwipe key={this.state.images.length} swipeOptions={{ auto:3000,callback: (index)=> this.changeActive(index) }}>
                   {this.showImage()}
                   
                </ReactSwipe>
                {/* swipe小圆点 */}
                <div className="swipe-num">
                    {this.showNum()}
                </div>
                {/* 精品课程 */}
                <div className="course">
                    <div className="paddLR">
                        <span className="big">视频推荐</span>
                        <small className="fr grey" onClick={()=>this.goCourse()}>更多视频</small>
                    </div>
                    <div className="course-content half">
                        {this.getCourseHalf()}
                    </div>
                    <div className="clear" />
                    <div className="course-content">
                        {this.getCourse()} 
                    </div>
                </div> 
                {/* 底部Tabbar */}
                <Tabbar selected="home"/>
            </div>
        )
    }
}

export default Home;


