import React, { Component } from 'react';
import Header from '../components/header';
import Tabbar from '../components/tabbar';
class Find extends Component{
    state = {
        lists: [{
            title: "关于一所",
            icon: require('../assets/img/faxian_lineclass.png'),
            children:[{
                title: '一所概况',
                text: '在中关村科技园南端白石桥旁矗立着一座高80米，庄重大气的现代化办公大楼，这里就是公安部第一研究所所在地。公安部第一研究所自1960年建立以来，与我国公安科技事业同发展，与社会公共安全事业同进步，始终坚持服务公安业务、服务公安一线、服务社会公共安全的宗旨。'
            },{
                title: '机构设置',
                text: '无'
            },{
                title: '证书展示',
                text: '涉及国家秘密的计算机信息系统集成资质证书 综合布线资质 保密安防监控资质 质量管理体系认证书'
            }]
        },{
            title: "新闻动态",
            icon: require('../assets/img/faxian_exam.png'),
            children: [{
                title:'全部',
                text: '公安部部长赵克志在我所宣讲党的十九大精神并调研时强调 深入学'
            },{
                title:'工作动态',
                text:'我所召开SVAC感知型摄像机发布会'
            },{
                title: '科研动态',
                text:'[科技周专题]居民身份证网上应用'
            }]
        },{
            title: "网上展厅",
            icon: require('../assets/img/faxian_teacher.png'),
            children:[{
                title: '系统解决方案',
                text: 'GYBSJX01-HHD 便携式（高清）审...'
            },{
                title:'安检技术',
                text:'神盾车底违禁物品安全检查系统'
            },{
                title:'证件技术',
                text:'ZD-CA3智能化身份信息安全管理系...'
            }]
        },{
            title: "资料下载",
            icon: require('../assets/img/faxian_library.png'),
            children:[{
                title:'全部',
                text:'身份证阅读器100系列随机光盘201'
            },{
                title:'一所文档',
                text:'公安部第一研究所服务公安业务需..'
            },{
                title:'机读设备',
                text:'ICR500G开发包'
            }]
        },{
            title: "所属公司",
            icon: require('../assets/img/faxian_topic.png'),
            children:[{
                title:'燕京科技',
                text:'北京燕京科技有限公司是公安部第一研究所的全资企业，具有独立法人资格；是集研发、生产、销售、培训、服务及工程施工为一体的综合性经济实体。公司的宗旨是：“科技为本，求实创新，用户至上，和谐发展”。'
            },{
                title:'中盾奥迪安',
                text:'北京中盾奥迪安科技有限公司成立于2005年9月21日，是由公安部第一研究所和以色利OTI公司共同出资的中外合资公司，注册资本498万美元'
            },{
                title:'中天锋',
                text:'北京中天锋安全防护技术有限公司成立于1996年，是公安部第一研究所出全资设立的国有独资企业，专门从事警、民用安全防护、消防、公安执法、警用智能终端等产品的研发、生产和销售。依托公安部第一研究所雄厚的经济基础和研发实力，公司目前已成为国内警用防护领域的龙头企业和综合警用装备集成供应商。'
            }]
        },{
            title: "技术机构",
            icon: require('../assets/img/faxian_group.png'),
            children:[{
                title:'检测中心',
                text:'公安部第一研究所检测中心，对外称为国家安全防范报警系统产品质量监督检验中心(北京) 、公安部安全与警用电子产品质量检测中心、公安部特种警用装备质量监督检验中心及神盾计量校准中心。'
            },{
                title:'中国安全技术防范认证中心',
                text:'公安部第一研究所（认证中心），对外称为中国安全技术防范认证中心（以下简称安防认证中心，英文缩写CSP），是依据《中华人民共和国认证认可条例》等相关法律、法规'
            },{
                title:'全国安全防范报警系统标准化技术委员会',
                text:'全国安全防范报警系统标准化技术委员会（以下简称安标委）成立于1987年.'
            }]
        }]
    }
    goFind = (item) => {
        let hrefStr = [];
        for(let i=0;i<item.children.length;i++){
            for(let j in item.children[i]){
              hrefStr.push(`${j}=${item.children[i][j]}`) 
            }
        }
        console.log(hrefStr)
        window.location=`#/fdetail?title=${item.title}&children=${encodeURI(JSON.stringify(item.children))}`;
    }
    getList = () => {
        return (
            this.state.lists.map((item,index) => 
                <li key={index} onClick={()=>this.goFind(item)}>
                    <img src={item.icon} alt={item.title} />
                    {item.title}
                    <i className="icon icon-youjiantou"></i>
                </li>
            )
        )
    }
    render(){
        return (
            <div className="Find">
                {/* 头部Header */}
                <Header />
                <ul className="more_list">
                    {this.getList()}            
                </ul>
                {/* 底部Tabbar */}
                <Tabbar selected="find" />
            </div>
        )
    }
}

export default Find;


