import React,{Component} from 'react';
import {Tabs} from 'antd-mobile';

import banner1 from '../../assets/img/banner1.jpg';
import banner2 from '../../assets/img/banner2.jpg';
import banner3 from '../../assets/img/banner3.jpg';

import Header from '../../components/header';
import BtnBottom from '../../components/btnbotm';

class MineVideo extends Component{
    state = {
        tabs:[ { 
            title: '我上传的', 
            sub: '1' 
        }, { 
            title: '我购买的', 
            sub: '2' 
        }],
        courses:[{
            name: '人工智能',
            desc: '开放的云模式构建繁荣的生态,云端智能化比前端智能化更强大、更丰富',
            price: '￥20.00',
            num: '1000',
            color: '#0070bb',
            src: banner2
        },{
            name: '物联网',
            desc: '进入云时代，安防行业引入IT领域的云计算、大数据、物联网等技术为己所用',
            price: '￥32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '大数据',
            desc: 'IT行业的开放创新、合作共赢的精神也在潜移默化的影响安防行业秩序',
            price: '￥23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '云计算',
            desc: '在通信和互联网圈的生态系统建设，也成为时下安防行业发展的一个热门课题',
            price: '￥40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        },{
            name: '物联网',
            desc: '进入云时代，安防行业引入IT领域的云计算、大数据、物联网等技术为己所用',
            price: '￥32.00',
            num: '1240',
            color:'#ff4545',
            src: banner3
        },{
            name: '大数据',
            desc: 'IT行业的开放创新、合作共赢的精神也在潜移默化的影响安防行业秩序',
            price: '￥23.00',
            num: '300',
            color:'#ff4545',
            src: banner1
        },{
            name: '云计算',
            desc: '在通信和互联网圈的生态系统建设，也成为时下安防行业发展的一个热门课题',
            price: '￥40.00',
            num: '2000',
            color:'#ff4545',
            src: banner2
        }]
    }
    getCourse = () => {
        return(
            this.state.courses.map((item,index) => 
             <div key={index} onClick={()=>this.goDetail(item)} className="paddLR">
                <div className="fl" style={{width:"38%",marginRight:"2%"}}>
                    <img src={item.src} alt="peitu" width="100%" style={{borderRadius:"10px",margin:"5% 0 0" }}/>
                </div>
                <div key={index}  className="fl text-l" style={{color:"#000",width:"60%"}} >
                    <div>{item.name}</div>
                    <small className="lineH-low">{item.desc}</small>
                    <div>
                        <small>继续学习</small>
                    </div>
                </div>
                <div className="clear" />
             </div>
            )
        )
    }
    goDetail = (item) => {
        let hrefStr = [];
        for(let i in item){
          hrefStr.push(`${i}=${item[i]}`) 
        }
        window.location=`#/detail?purchase=0&${encodeURI(hrefStr.join('&'))}`;
    }
    btnCLick = () => {
        console.log('上传视频')
        window.location = '#/upload'
    }
    render(){
        const {tabs} = this.state; 
        return (
            <div className="MineVideo">
                <Header back title="我的视频" />
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    {/* 符合条件的列表 */}
                    <div className="course">
                        <div className="course-content">
                            {this.getCourse()} 
                        </div>
                    </div>                 
                </Tabs>
                 
                <BtnBottom click={()=>this.btnCLick} title="上传视频" />
            </div>
        )
    }
}
export default MineVideo;


