import React, { Component } from 'react';
import { Tabs, Accordion, List, Modal, Toast } from 'antd-mobile';

import bg from '../assets/img/detail-bg.jpg';
import Header from '../components/header';
import BtnBottom from '../components/btnbotm';
class Detail extends Component{
    componentDidMount(){
        let param =decodeURI(window.location.hash.split('?')[1]).split('&'),
        paramObj = {},
        videobg = '',
        purchase = false;

        for(let i = 0; i<param.length; i++){
            let key = param[i].split('=')[0],
            value = param[i].split('=')[1];
            paramObj[key] = value;
        }
        //视频封面和是否显示收费设置
        (paramObj.src)? videobg = paramObj.src : videobg = bg;
        (paramObj.purchase) ? purchase = true : purchase = false;

        
        this.setState({
            item: paramObj,
            videobg: videobg,
            purchase: purchase
        })
    }
    state = {
        item: {},
        purchase: false,
        videobg:bg,
        tabs: [{ 
            title:"介绍"
        }, { 
            title: "章节"
        },{ 
            title: "点评"
        }],
        showVideo: false
    }
    onChange = (key) => {
        console.log(key);
    }
    btnCLick = () => {
        if(!this.state.purchase){
            const alert = Modal.alert;
            alert('支付提示', `您还未购买此视频，是否支付${this.state.item.price} 购买？`, 
            [{ 
                    text: '取消', 
                    onPress: () => console.log('cancel') 
                }, { 
                    text: '确认', 
                    onPress: () => {
                        Toast.loading('购买中...',1)
                        setTimeout(() => {
                            Toast.success('购买成功',.5);
                            this.setState({
                                showVideo: true
                            })
                        },1000) 
                    }
                }]
            )
        }else{
            this.setState({
                showVideo: true
            })
        }
       
    }
    render(){
        const {item,tabs,videobg,showVideo} = this.state;
        return (
            <div className="Detail">
                {/* 头部Header */}
                <Header back title={item.name}/>
                <div className="detail-header"  style={{background:`url(${videobg}) no-repeat `,backgroundSize:"cover"}}>
                    <div className={showVideo?"menu-hide":"menu-show detail-msg"}>
                        <h2 className="text-center">{item.name}</h2>
                        <p>
                            {item.desc}
                        </p>
                    </div>
                    <div className={showVideo?"menu-hide":"menu-show cover"}></div>
                    
                    <div className={!showVideo?"menu-hide":"menu-show detail-cover"}>
                        <video src={require("../assets/H.mp4")} width="100%" controls poster=""></video>
                    </div>
                </div>
                {/* tabs */}
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        {/* <h3>课程详情</h3> */}
                        <h2>{item.name}</h2>
                        <div>
                            {item.desc}
                        </div>
                        <small>
                            价格{item.price} |
                            已有{item.num}人学习
                        </small>
                        <h3>课程内容</h3>
                        <div>
                            视频云建设模式成主流 <br/>
                            1. 提升资源使用效率: 计算和存储的资源池化，可以大幅度的提升资源使用效率， 实现资源的分时、跨域共享和按需调用，避免重复建设，同时实现资源的快速弹性扩容，加快业务部署。<br/>
                            2. 为数据的融通提供可能: 便于市、区、县之间视频监控数据的融通，形成“数据湖”，实现视频监控图像信息共享，使跨市、区、县的视频追踪办案成为可能。<br/>
                            3. 解决海量视频图像信息大数据和AI处理的算力问题: 视频监控图像信息大数据和AI的处理、分析需要用到超高性能计算能力，上云可以解决算力不足的问题.
                        </div>
                    </div>
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        <h3>章节详情</h3>
                        <Accordion defaultActiveKey="0" className="my-accordion" onChange={this.onChange}>
                            <Accordion.Panel header="第一章">
                                <List className="my-list">
                                    <List.Item>第一节</List.Item>
                                    <List.Item>第二节</List.Item>
                                    <List.Item>第三节</List.Item>
                                </List>
                            </Accordion.Panel>
                            <Accordion.Panel header="第二章" className="pad">
                            <List className="my-list">
                                    <List.Item>第一节</List.Item>
                                    <List.Item>第二节</List.Item>
                                    <List.Item>第三节</List.Item>
                                </List>
                            </Accordion.Panel>
                            <Accordion.Panel header="第三章" className="pad">
                            <List className="my-list">
                                    <List.Item>第一节</List.Item>
                                    <List.Item>第二节</List.Item>
                                    <List.Item>第三节</List.Item>
                                </List>
                            </Accordion.Panel>
                        </Accordion>
                    </div>
                    <div style={{ padding:'20px', backgroundColor: '#fff' }}>
                        <h3>点评详情</h3>
                        <p>无</p>
                    </div>
                </Tabs>
                <div  className={showVideo?"menu-hide":"menu-show"} >
                    <BtnBottom click={()=>this.btnCLick} title="观看视频"/>
                </div>
            </div>
        )
    }
}

export default Detail;


