import React,{Component} from 'react';
import Header from '../components/header';

class Search extends Component{
    shap = () => {

    }
    gosearch = () => {
      window.location=`#/course`;
    }
    render(){
        return(
            <div className="search-bodyer">
                {/* 头部Header */}
                <Header back/>
                <div className="goods-eac-box">
                    <div className="eac-per-ens">
                        <div className="aingstoit">
                            <input value="video" id="search_cate" type="hidden" disabled="" />
                        </div>
                        <input autoFocus={true} onInput={this.shap()} id="search_combo_input" />
                        <i className="icon icon-quxiao"></i>
                    </div>
                    <button id="btn_search_site" onClick={()=>this.gosearch()}>搜索</button>
                </div>

                <div className="headlines">
                    <h5>大家都在搜</h5>
                    <ul id="video_list">
                        <li onClick={()=>window.location='#/detail?name=人工智能'}>人工智能</li>
                        <li onClick={()=>window.location='#/detail?name=大数据'}>大数据</li>
                        <li onClick={()=>window.location='#/detail?name=云计算'}>云计算</li>
                        <li onClick={()=>window.location='#/detail?name=物联网'}>物联网</li>
                        <li onClick={()=>window.location='#/detail?name=移动互联'}>移动互联</li>
                        <li onClick={()=>window.location='#/detail?name=视频云平台'}>视频云平台</li>
                    </ul>
                </div>
            </div>
        )
    }
}
export default Search


